* {
    margin: 0;
    padding: 0;

}

body {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
        'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
        sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

.home {
    display: flex;
    flex-direction: column;
    height: 100vh;
}

.home__header {
    display: flex;
    justify-content: space-between;
    padding: 20px 30px;
    align-items: center;
}

.home__header a {
    margin-right: 20px;
    text-decoration: inherit;
    color: rgba(0, 0, 0, 87);
    font-size: 15px;
}

.home__header a:hover {
    text-decoration: underline;
}

.home__header__right {
    display: flex;
    align-items: center;
    min-width: 13vw;
    justify-content: space-between;
}

.home__header__right>.MuiSvgIcon-root {
    margin-right: 20px;
}

.home__body {
    flex: 1;
    display: flex;
    margin-top: 10%;
    flex-direction: column;
}

.home__body>img {
    object-fit: contain;
    height: 100px;
}

/* Search */
.search__input {
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    height: 30px;
    padding: 10px 20px;
    border-radius: 999px;
    width: 75vw;
    margin: 0 auto;
    margin-top: 40px;
    max-width: 560px;
}

.search__input>input {
    flex: 1;
    padding: 10px 20px;
    font-size: medium;
    border: none;
}

.search__input>input:focus {
    outline-width: 0px;
}

.search__nput__icon {
    color: gray;
}

.search__mic__icon {
    cursor: pointer;
}

.search__buttons {
    margin-top: 30px;
    display: flex;
    justify-content: center;
}

.search_hide_buttons {
    display: none !important;
}

.search__buttons button {
    margin: 5px;
    padding: 7px 15px;
    background-color: #f8f8f8;
    text-transform: inherit;
    border: 1px solid white;
    color: #5f6368;
}

.search__buttons button:hover {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    background-image: -webkit-linear-gradient(top, #f8f8f8);
    background-color: #f8f8f8;
    border: 1px solid #c6c6c6;
    color: #222;
}

/* Search Page */
.search__page__header {
    display: flex;
    top: 0;
    background-color: #fff;
    z-index: 100;
    align-items: flex-start;
    padding: 30px;
    border-bottom: 1px solid lightgray;
}

.search__page__logo {
    object-fit: contain;
    height: 50px;
    margin-right: 30px;
}

.search__page__header__body>.search>.search__input {
    margin-top: 0px;
    width: unset;
    margin: unset;
    min-width: unset;
}

.search__page__options {
    margin-top: 20px;
    margin-bottom: -10px;
    display: flex;
    align-items: center;
    color: gray;
}

.search__page__option {
    display: flex;
    align-items: center;
    margin-right: 20px;
}

.search__page__option a {
    text-decoration: none;
    color: gray;
    margin-left: 5px;
}

.search__page__option_left,
.search__page__option_right {
    display: flex;
}

.search__page__option_right {
    margin-left: 100px;
}

.search__page__header__right__icons {
    display: flex;
    align-items: center;
    min-width: 6vw;
    justify-content: space-between;
    margin-left: 16%;
}

.search__page__results{
    min-width: 650px;
    margin: 20px 50px 100px 210px;
}
.search__page__result{
    margin-bottom: 20px;
}
.search__page__result__count{
    color: #70757a;
    font-size: 18px;
    margin-bottom: 20px;
}
.search__page__result_website{
    color: #31353d;
    text-decoration: none;
}
.search__page__result_title{
    text-decoration: none;
}
.search__page__result_title:hover{
    text-decoration: underline;
}
.search__page__result_title > h2 {
    font-weight: 400;
}
.search__page__result_snippet{
    margin-top: 10px;
}